input[type=checkbox] {
  display: none; }

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 44px;
  height: 24px;
  background: grey;
  display: block;
  border-radius: 100px;
  position: relative; }

label:after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50px;
  transition: 0.3s; }

input:checked + label {
  background: $primary; }

input:checked + label:after {
  left: calc(100% - 2px);
  transform: translateX(-100%); }

label:active:after {
  width: 30px; }

