  color: #1D1D1F;
  appearance: none;
  border-radius: 4px;
  font-family: 'Gilroy';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  &:after {
    background-image: url('../img/Select.svg'); } }

.select-arrow-active {
  background-image: url('../img/Select2.svg'); }

.select-items div, .select-selected {
  color: $text-main;
  height: 56px;
  padding: 16px;
  border: 1px solid $stroke;
  border-right: none;
  border-top: none;
  cursor: pointer;
  user-select: none; }

.select-items {
  height: 309px;
  position: absolute;
  background-color: #FFFFFF;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
  overflow: auto; }

.select-hide {
  display: none; }

.select-items div:hover, .same-as-selected {
  color: $primary; }

::-webkit-scrollbar {
  width: 13px;
  padding: 4px;
  margin: 0;
  background-color: #fff;
  border-radius: 7px; }

::-webkit-scrollbar-thumb {
  border: 5px solid transparent;
  background-color: $primary;
  border-radius: 15px;
  background-clip: content-box; }

::-webkit-scrollbar-button {
  display: none;
  width: 0;
  height: 0; }

::-webkit-scrollbar-corner {
  background-color: $primary; }

.errorValidate {
  border: 1px solid $red; }

@media (max-width: 376px) {
  body {
    max-width: 375px; } }

